"use client"

import type React from "react"
import { useEffect, useRef, useState } from "react"
import { usePantryAssistantStore } from "@/app/context/PantryAssistantContext"
import { X, Send, Package } from "lucide-react"
import { Button } from "@/components/ui/button"
import { Input } from "@/components/ui/input"
import { Card } from "@/components/ui/card"
import { chatWithPantryAssistant } from "@/lib/geminiPantry"
import { toast } from "sonner"
import Cookies from "js-cookie"
import { useRouter } from "next/navigation"

export function PantryAssistantModal() {
  const { isOpen, setIsOpen, messages, addMessage, clearMessages, isLoading, setIsLoading, dispensaId } =
    usePantryAssistantStore()

  const [inputValue, setInputValue] = useState("")
  const messagesEndRef = useRef<HTMLDivElement>(null)
  const router = useRouter()

  useEffect(() => {
    messagesEndRef.current?.scrollIntoView({ behavior: "smooth" })
  }, [messages])

  const handleSendMessage = async (e: React.FormEvent) => {
    e.preventDefault()

    if (!inputValue.trim() || !dispensaId) {
      toast.error("Selecione uma dispensa primeiro")
      return
    }

    const userMessage = {
      id: Date.now().toString(),
      role: "user" as const,
      content: inputValue,
      timestamp: new Date(),
    }

    addMessage(userMessage)
    setInputValue("")
    setIsLoading(true)

    try {
      const response = await chatWithPantryAssistant(inputValue, dispensaId)

      // Parse the JSON response
      let parsedResponse
      try {
        parsedResponse = JSON.parse(response)
      } catch {
        throw new Error("Resposta inválida do assistente")
      }

      // Add items to pantry
      if (parsedResponse.items && Array.isArray(parsedResponse.items)) {
        for (const item of parsedResponse.items) {
          await fetch(`${process.env.NEXT_PUBLIC_URL_API}/alimentos/dispensa/${dispensaId}/alimentos`, {
            method: "POST",
            headers: {
              "Content-Type": "application/json",
              Authorization: "Bearer " + Cookies.get("token") || "",
            },
            body: JSON.stringify({
              ...item,
              dispensaId: dispensaId,
            }),
          })
        }

        toast.success("Itens adicionados com sucesso!", {
          style: {
            background: "#00c950",
            color: "#ffffff",
          },
        })

        // Refresh the page after a short delay
        setTimeout(() => {
          router.refresh()
        }, 1000)
      }

      const assistantMessage = {
        id: (Date.now() + 1).toString(),
        role: "assistant" as const,
        content: parsedResponse.message || "Itens processados com sucesso!",
        timestamp: new Date(),
      }
      addMessage(assistantMessage)
    } catch (error) {
      toast.error("Erro ao processar sua solicitação")
      console.error(error)
      const errorMessage = {
        id: (Date.now() + 1).toString(),
        role: "assistant" as const,
        content: "Desculpe, ocorreu um erro ao processar sua solicitação. Tente novamente.",
        timestamp: new Date(),
      }
      addMessage(errorMessage)
    } finally {
      setIsLoading(false)
    }
  }

  if (!isOpen) return null

  return (
    <div className="fixed inset-0 bg-black/40 flex items-end justify-end z-50 p-4">
      <Card className="w-full max-w-md h-[600px] flex flex-col bg-white border border-slate-200 shadow-xl">
        <div className="flex items-center justify-between p-4 border-b border-slate-200">
          <div className="flex items-center gap-2">
            <Package className="w-5 h-5 text-green-600" />
            <h3 className="font-semibold text-slate-900">Assistente de Dispensa</h3>
          </div>
          <button onClick={() => setIsOpen(false)} className="text-slate-500 hover:text-slate-700">
            <X className="w-5 h-5" />
          </button>
        </div>

        <div className="flex-1 overflow-y-auto p-4 space-y-4">
          {messages.length === 0 ? (
            <div className="flex flex-col items-center justify-center h-full text-center px-4">
              <Package className="w-16 h-16 text-green-600 mb-4" />
              <p className="text-slate-900 font-medium mb-2">Adicione itens à sua dispensa</p>
              <p className="text-slate-500 text-sm">
                Descreva os itens que deseja adicionar. Você pode adicionar múltiplos itens de uma vez!
              </p>
              <div className="mt-4 text-left bg-slate-50 p-3 rounded-lg text-xs text-slate-600">
                <p className="font-medium mb-1">Exemplos:</p>
                <p>• "Adiciona 2kg de arroz e 1 litro de leite"</p>
                <p>• "5 maçãs que vencem dia 30/01"</p>
                <p>• "1 pacote de macarrão"</p>
              </div>
            </div>
          ) : (
            messages.map((message) => (
              <div key={message.id} className={`flex ${message.role === "user" ? "justify-end" : "justify-start"}`}>
                <div
                  className={`max-w-xs px-4 py-2 rounded-lg ${
                    message.role === "user"
                      ? "bg-green-600 text-white rounded-br-none"
                      : "bg-slate-100 text-slate-900 rounded-bl-none"
                  }`}
                >
                  <p className="text-sm whitespace-pre-wrap">{message.content}</p>
                  <span className="text-xs opacity-70 mt-1 block">
                    {new Date(message.timestamp).toLocaleTimeString("pt-BR", {
                      hour: "2-digit",
                      minute: "2-digit",
                    })}
                  </span>
                </div>
              </div>
            ))
          )}
          {isLoading && (
            <div className="flex justify-start">
              <div className="bg-slate-100 text-slate-900 px-4 py-2 rounded-lg rounded-bl-none">
                <div className="flex gap-2">
                  <div className="w-2 h-2 bg-slate-400 rounded-full animate-bounce"></div>
                  <div
                    className="w-2 h-2 bg-slate-400 rounded-full animate-bounce"
                    style={{ animationDelay: "0.1s" }}
                  ></div>
                  <div
                    className="w-2 h-2 bg-slate-400 rounded-full animate-bounce"
                    style={{ animationDelay: "0.2s" }}
                  ></div>
                </div>
              </div>
            </div>
          )}
          <div ref={messagesEndRef} />
        </div>

        <form onSubmit={handleSendMessage} className="p-4 border-t border-slate-200 flex gap-2">
          <Input
            value={inputValue}
            onChange={(e) => setInputValue(e.target.value)}
            placeholder="Ex: Adiciona 2kg de arroz..."
            className="flex-1 bg-slate-50 border-slate-200 text-slate-900"
            disabled={isLoading}
          />
          <Button
            type="submit"
            disabled={isLoading || !inputValue.trim()}
            className="bg-green-600 hover:bg-green-700 text-white"
          >
            <Send className="w-4 h-4" />
          </Button>
        </form>
      </Card>
    </div>
  )
}
